@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;500;600&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --qbtc-red: #8B0000;
  --qbtc-blue: #00FFFF;
  --qbtc-pink: #FF1493;
  --qbtc-dark: #0A0A0A;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--qbtc-dark);
  color: white;
  overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Orbitron', sans-serif;
}

code, pre {
  font-family: 'Roboto Mono', monospace;
}

.neon-text {
  text-shadow: 0 0 5px var(--qbtc-blue), 0 0 10px var(--qbtc-blue), 0 0 15px var(--qbtc-blue);
}

.neon-border {
  box-shadow: 0 0 5px var(--qbtc-blue), 0 0 10px var(--qbtc-blue);
}

.circuit-bg {
  background-color: var(--qbtc-dark);
  background-image: radial-gradient(rgba(255, 20, 147, 0.1) 1px, transparent 1px);
  background-size: 30px 30px;
}

.gradient-text {
  background: linear-gradient(90deg, var(--qbtc-blue), var(--qbtc-pink));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.section {
  min-height: 100vh;
  padding: 4rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media (max-width: 768px) {
  .section {
    padding: 2rem 1rem;
  }
}

/* Animation classes */
.animate-float {
  animation: float 6s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

.animate-pulse-slow {
  animation: pulse 3s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.7; }
}

/* Particle background */
.particles-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
